export default {
	data () {
		return{
			computedTimer: 0,
			getYzm: true,
			userPhone: '',
			code: '',
			yzm: ''
		}
	},
	methods: {
		//获取验证码
		async getCode () {
			if (!this.getYzm) return
			
			if(this.userPhone.trim() == '' ) {
				uni.showToast({
					icon: 'none',
					title: '请先输入手机号'
				})
				return
			} else if (this.userPhone.length != 11 ) {
				uni.showToast({
					icon: 'none',
					title: '请检查手机号输入是否正确'
				})
				return
			}
			this.getYzm = false 
			this.computedTimer = 60
			const timer = setInterval(() => {
				if (this.computedTimer === 0) {
					clearInterval(timer)
					this.getYzm = true
				} else {
					this.computedTimer--
				}
			},1000)
			let res = await this.$fetch(this.$api.customerPhone,{mobile:this.userPhone},'GET','form')
			if (res.code === 0) {
				this.code = res.msg
			}
			
		},
		//提交
		async submit () {
			let token = uni.getStorageSync('token')
			if (this.userPhone.trim() == '' || this.yzm.trim() === '') {
				uni.showToast({
					icon: 'none',
					title: '请先填写相关信息'
				})
				return
			}
			
			
			let res = await this.$fetch(this.$api.updMobile,{checkCode:this.yzm,mobile:this.userPhone,token:token},"POST","form")
		
			if (res.code === 0) {
				uni.showToast({
					icon: 'success',
					title: '修改成功'
				})
				uni.removeStorageSync('token');
				
				setTimeout(() => {
					uni.reLaunch({
						url:'../enter/enter'
					})
				},1500)
			} else {
				uni.showToast({
					icon: 'none',
					title: res.msg
				})
			}
		}
	}
}
